import { globalState } from "../state/index";
import { generateTaskId } from "../utils/tasks";
/**
 * 
 */
export const getPreloadData = () => new Promise((resolve, reject) => {
    const taskId = generateTaskId();
    globalState.promiseMap.draftDataSync[taskId] = {
        resolve: resolve,
        reject,
    };
    const messageAction = {
        type: "GET_PRELOAD_DATA",
        payload: {
            taskId: taskId,
        },
    };
    window.ContributionEditWebView.postMessage(JSON.stringify(messageAction));
});
/**
 * 
 */
export const onRichTextEditorFocused = () => {
    const messageAction = { type: "CLICKED_ON_BOM_WINDOW" };
    window.ContributionEditWebView.postMessage(JSON.stringify(messageAction));
};
/**
 * 
 * @param materialId
 */
export const onMaterialRemove = (materialId) => {
    const messageAction = {
        type: "ON_MATERIAL_REMOVE",
        payload: { materialId: materialId },
    };
    window.ContributionEditWebView.postMessage(JSON.stringify(messageAction));
};
/**
 *  HTML id  flutter
 */
export const syncRichTextAttribToFlutter = () => {
    const messageAction = {
        type: "SYNC_RICH_TEXT_ATTRIB_DATA",
        payload: {
            richTextHTMLContent: window.__nativeHtml5.getHTML(),
            idList: window.__nativeHtml5.getMediaIds(),
        },
    };
    window.ContributionEditWebView.postMessage(JSON.stringify(messageAction));
};
export const onGetPreloadDataReceived = (action) => {
    globalState.promiseMap.draftDataSync[action.taskId].resolve(action.payload);
    delete globalState.promiseMap.draftDataSync[action.taskId];
};
/**
 * webView 
 */
export const onRichTextAttribDataRequested = () => {
    syncRichTextAttribToFlutter();
};
