import { globalState } from "../state/index";
import { generateTaskId } from "../utils/tasks";
/**
 *  App 
 * @param materialType 
 * @param existedMaterialCount 
 * @param callback
 */
export const selectMaterialFromAlbum = (materialType, existedMaterialCount, callback) => new Promise((resolve, reject) => {
    const taskId = generateTaskId();
    globalState.promiseMap.materialSelection[taskId] = {
        resolve: resolve,
        reject,
    };
    const messageAction = {
        type: "SELECT_MATERIAL_FROM_ALBUM",
        payload: {
            taskId: taskId,
            materialType: materialType,
            existedMaterialCount: existedMaterialCount,
        },
    };
    globalState.materialSelectionCallbackMap[taskId] = callback;
    window.ContributionEditWebView.postMessage(JSON.stringify(messageAction));
});
/**
 *    webview 
 * @param action
 */
export const onMaterialSelectCompletionMsgReceive = (action) => {
    globalState.promiseMap.materialSelection[action.taskId].resolve(action.payload);
    delete globalState.promiseMap.materialSelection[action.taskId];
};
/**
 *  
 * @param action
 */
export const onMaterialSelectionCallbackTriggered = (action) => {
    const { success, data, message } = action.payload;
    // 
    globalState.materialSelectionCallbackMap[action.taskId](data, success ? null : message);
    // 
    if (data.every((selectedMaterial) => selectedMaterial.progress === 100)) {
        delete globalState.materialSelectionCallbackMap[action.taskId];
    }
};
/**
 * 
 * @param id id
 */
export const removeMaterialFromUploadingList = (id) => {
    const messageAction = {
        type: "REMOVE_MATERIAL_FROM_UPLOADING_LIST",
        payload: { id },
    };
    window.ContributionEditWebView.postMessage(JSON.stringify(messageAction));
};
/**
 * 
 * @param payload
 */
export const previewMaterial = (payload) => {
    const messageAction = {
        type: "PREVIEW_REMOTE_MATERIAL",
        payload: payload,
    };
    window.ContributionEditWebView.postMessage(JSON.stringify(messageAction));
};
