import { onMaterialSelectCompletionMsgReceive, onMaterialSelectionCallbackTriggered, } from "./materialSelect/index";
import { onGetPreloadDataReceived, onRichTextAttribDataRequested } from "./stateSync/index";
import { WebViewMsgType } from "./types";
export { selectMaterialFromAlbum, removeMaterialFromUploadingList, previewMaterial } from "./materialSelect/index";
export { SelectedMaterialType } from "./materialSelect/types";
export { getPreloadData, onRichTextEditorFocused, onMaterialRemove } from './stateSync/index';
/**
 *  webview 
 *
 *  flutter 
 * @param msg 
 */
window.onWebViewMsgReceive = (msg) => {
    switch (msg.type) {
        case WebViewMsgType.SELECT_MATERIAL:
            onMaterialSelectCompletionMsgReceive(msg);
            break;
        case WebViewMsgType.TRIGGER_MATERIAL_SELECTION_CALLBACK:
            onMaterialSelectionCallbackTriggered(msg);
            break;
        case WebViewMsgType.GET_PRELOAD_DATA_CALLBACK:
            onGetPreloadDataReceived(msg);
            break;
        case WebViewMsgType.REQUEST_RICH_TEXT_ATTRIB_DATA:
            onRichTextAttribDataRequested();
            break;
    }
};
